<template>
<TabContent style="height: 100%">
<v-container fill-height>
<v-row justify="center" align="center" class="text-center">
<v-container style="max-width: 600px">
  <h1>Калькулятор смены срока аренды</h1><br>
  <v-row>
    <v-col cols="12" md="4">
      <v-text-field v-model="oldEndDate" label="Старая дата окончания"/>
    </v-col>
    <v-col cols="12" md="4">
      <v-text-field v-model="oldPrice" label="Старая стоимость"/>
    </v-col>
    <v-col cols="12" md="4">
      <v-text-field v-model="newPrice" label="Новая стоимость"/>
    </v-col>
  </v-row>
  <v-text-field v-model="result" label="Новая дата окончания" solo flat/>
  <div>День по старой цене: {{oldDayPrice}}₽</div>
  <div>День по новой цене: {{newDayPrice}}₽</div>
  <div>Дней оставалось: {{days}}</div>
  <div>Дней останется: {{newDays}}</div>
</v-container>
</v-row>
</v-container>
</TabContent>
</template>

<script>
import TabContent from '@/render/components/Common/TabContent.vue'

export default {
  components: { TabContent },
  props: ['value'],
  data: () => ({
    oldEndDate: '2021-12-12',
    oldPrice: 100,
    newPrice: 50
  }),

  mounted () {
    const newDate = new Date(Date.now() - new Date().getTimezoneOffset() + 3600e3 * 3 + 86400e3 * 10)
    this.oldEndDate = this.value.query.oldEndDate || `${newDate.getFullYear()}-${newDate.getMonth() + 1}-${newDate.getDate()}`
    this.oldPrice = this.value.query.oldPrice || 100
    this.newPrice = this.value.query.newPrice || 50
    this.value.title = 'RedateCalculator'
    this.value.color = 'brown'
  },

  computed: {
    oldDayPrice () {
      return Math.floor(this.oldPrice / 31 * 100) / 100
    },

    newDayPrice () {
      return Math.floor(this.newPrice / 31 * 100) / 100
    },

    days () {
      const date = Date.now()
      const endTimestamp = Math.ceil(Math.abs(Date.parse(this.oldEndDate)))
      return Math.floor((endTimestamp - date) / 24 / 60 / 60 / 1000)
    },

    newDays () {
      return Math.floor(this.days * (this.oldDayPrice / this.newDayPrice))
    },

    result () {
      const newDate = new Date(Date.now() - new Date().getTimezoneOffset() + 3600e3 * 3 + 86400e3 * this.newDays + 86400e3)
      return `${newDate.getFullYear()}-${newDate.getMonth() + 1}-${newDate.getDate()}`
    }
  },

  watch: {
    result (result) {
      this.value.fullTitle = `${this.endDate} / ${this.price}₽ = ${result}₽`
    }
  }
}
</script>
